.change-log-dialog {
    .p-datatable-wrapper .p-dialog-content .card {
        max-height: calc(100vh - 182px) !important;
    }
    .p-dialog {
        .p-dialog-header {
            padding: 7px 10px 7px 8px;
        }
    }
    .gridWrapperTyr {
        max-height: none !important;
    }
    thead.p-datatable-thead {
        tr:nth-child(2) th {
            padding-top: 5px;
            padding-bottom: 5px;
        }
        th {
            padding: 9px 9px;
            background: #D9D9D9 !important;
            .p-inputtext {
                height: 24px;
            }
            .p-column-filter-menu-button, .p-column-filter-clear-button {
                height: 1.3rem;
                width: 1.3rem;
                margin-left: 1px;
            }
        }
    }
    tbody.p-element.p-datatable-tbody tr td{
        padding: 0px 9px;
    }
}

.mdlChange-log-dialog .p-paginator-current,
.mdlChange-log-dialog .p-paginator-first,
.mdlChange-log-dialog .p-paginator-prev,
.mdlChange-log-dialog .p-paginator-pages,
.mdlChange-log-dialog .p-paginator-last,
.mdlChange-log-dialog .p-paginator-next {
    display: block !important;
    position: static !important;
}



